import { SecurityContext } from "../services/api/droppableService";
import { initWidget } from "./widget";

const widgetEntry = process.env.WIDGET_ENTRY || "revisionFloat"; // Default to revisionFloat
const response = await SecurityContext();
const securitycontext = response.securitycontextpreference;
const email = response.email;



initWidget(
  (widget) => {

    widget.setTitle("");

    // Add Security Context Preference (UI for SecurityContext)
    widget.addPreference(securitycontext);
    // Define email preference with a unique key and proper default value (Boolean for checkbox)
    let emailPreference = {
      name: "Email Notification",           // This can serve as a title or internal name
      type: "boolean",                     // Checkbox type for Boolean value
      label: "Email Notification",          // Label shown on preference UI
       defaultValue: false,                  // Default value (false means unchecked)
    };

    // Add the email preference so its UI appears separately from Security Context
    widget.addPreference(emailPreference);
   

    // Set the email value (if your widget logic uses this value)
    widget.setValue("email", email);
  

    // Optionally log all current preferences (if accessible)
    if (widget.preferences) {
      console.log("[widget-starter] Current widget preferences:", widget.preferences);
    }

    // Dynamically load widget index based on widgetEntry
    if (widgetEntry === "plantAssignment") {
      import("../plantIndex")
        .then((module) => {
          module.default();
        })
        .catch((error) => {
          console.error("[widget-starter] Error dynamically importing plantIndex", error);
          widget.body.innerHTML = "<div style='color: red;'>Error loading widget content.</div>";
        });
    } else if (widgetEntry === "massUpload") {
      import("../massuUploadIndex")
        .then((module) => {
          module.default();
        })
        .catch((error) => {
          console.error("[widget-starter] Error dynamically importing massuUploadIndex", error);
          widget.body.innerHTML = "<div style='color: red;'>Error loading widget content.</div>";
        });
    } else {
      import("../index")
        .then((module) => {
          module.default();
        })
        .catch((error) => {
          console.error("[widget-starter] Error dynamically importing index", error);
          widget.body.innerHTML = "<div style='color: red;'>Error loading widget content.</div>";
        });
    }
  },
  (error) => {
    console.error("[widget-starter] initWidget encountered an error:", error);
  }
);
